@keyframes slide-left {
  0% {
    transform: translateX(0);
    opacity: 0;
  }
  100% {
    transform: translateX(-20px);
    opacity: 1;
  }
}

.animate-slide-left {
  animation: slide-left 0.3s ease-out forwards;
}



@keyframes underline-in {
  from {
    transform: scaleX(0);
  }
  to {
    transform: scaleX(1);
  }
}

@keyframes underline-out {
  from {
    transform: scaleX(1);
  }
  to {
    transform: scaleX(0);
  }
}

@keyframes bump-in {
  0% { transform: rotate(0); }
  100% { transform: rotate(90deg) scale(1.1); }
}

@keyframes bump-out {
  0% { transform: rotate(90deg) scale(1.1); }
  100% { transform: rotate(0); }
}

.hover\:animate-bump:hover {
  animation: bump-in 0.5s cubic-bezier(0.68, -0.55, 0.27, 1.55) forwards;
}

.hover\:animate-bump:hover::after {
  animation: bump-out 0.5s cubic-bezier(0.68, -0.55, 0.27, 1.55) forwards;
}

.underline-animation {
  position: relative;
  display: inline-block;
}

.underline-animation::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 100%;
  height: 1.5px !important;
  background-color: #31313196;
  transform-origin: center;
  transform: scaleX(0);
  transition: transform 0.3s;
}

.underline-animation:hover::after {
  transform: scaleX(1);
}

